/** UI **/
.no-webp .inicio {
  background-image: url(../img/3.jpg);
}

.webp .inicio {
  background-image: url(../img/3.webp);
}

.header {
  background-color: #d2d7da;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem;
  background-color: #545757;
  margin-bottom: 0;
}
.footer .contenedo-footer {
  margin-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyringht {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .barra img {
    max-width: 100%;
    width: 28rem;
    height: auto;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .icono-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.iconos {
  display: flex;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: minmax(0, auto) minmax(0, auto) minmax(0, auto);
    grid-template-rows: minmax(0, auto);
    gap: 1rem;
  }
}
.anuncio {
  background-color: rgb(223.993902439, 227.4390243902, 229.506097561);
  border: 2px solid rgb(79.8, 82.65, 82.65);
  margin-bottom: 2rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-height: 25rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.imagen-anuncio {
  height: 30rem;
}
@media (min-width: 768px) {
  .imagen-anuncio {
    object-fit: cover;
    object-position: center;
  }
}

.contenido-anuncio {
  padding: 2rem;
  display: grid;
  grid-template-columns: minmax(0, auto);
  grid-template-rows: minmax(0, auto) minmax(0, 18rem) minmax(0, 8rem) minmax(0, 8rem);
  gap: 0.3rem;
}
.contenido-anuncio h3 {
  grid-row: 1/2;
}
.contenido-anuncio .parrafo {
  display: inline-flex;
  min-height: 160px;
  margin: 0;
}

.no-webp .imagen-contacto {
  background-image: url("../img/contac.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/contac.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #964d30;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #964d30;
  margin-top: 1rem;
}

.testimoniales {
  background-color: #c39b82;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimoniales blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimoniales blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimoniales p {
  text-align: right;
  color: #FFFFFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #545757;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #545757;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #b2b6b8;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/** Paginas internas**/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}

p {
  text-align: left;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(148, 17, 17);
}
.alerta.exito {
  background-color: #b2b6b8;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #c39b82;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}